    position: relative;
    width: 200px;
    height: 200px; }
    .preloader .preloader-content .preloader-first,
    .preloader .preloader-content .preloader-second,
    .preloader .preloader-content .preloader-third {
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      animation: 1s linear infinite; }
    .preloader .preloader-content .preloader-first {
      border-bottom: 3px solid #ff7473;
      animation-name: first; }
    .preloader .preloader-content .preloader-second {
