@mixin text-h2 {
  font-family: "Muller";
  font-weight: 700;
  color: #7f38bd;
  font-size: 30px;
}

@mixin text-h3 {
  font-size: 32px;
  font-family: "Muller";
  font-weight: 500;
  color: #383d52;
}

@mixin text {
  font-size: 14px;
  font-family: "Open Sans";
  font-weight: 400;
  color: #5e606a;
  line-height: 1.6;
}

@mixin button {
  border: none;
  background-color: #fb0275;
  border-radius: 25px;
  span {
    font-family: "Open Sans";
    font-weight: 700;
    font-style: normal;
    color: white;
    text-transform: uppercase;
    font-size: 16px;
  }
}

@mixin small-lines {
  background-color: #883eca;
  width: 2px;
  height: 82px;
  position: absolute;
  top: -60px;
  left: 100%;
  z-index: 3;
  &::after {
    content: "";
    background-color: #883eca;
    width: 2px;
    height: 82px;
    position: absolute;
    top: 40px;
    left: 5px;
    z-index: 3;
  }
}

@mixin number {
  font-family: "Muller";
  font-size: 200px;
  font-weight: 900;
  text-shadow: 0 0 15px #f1ebf2;
  color: #faf8fb;
  position: absolute;
  z-index: -5;
}

@mixin big-form {
  .text {
    font-family: "Open Sans";
    text-align: left;
    span {
      font-size: 16px;
      font-style: italic;
      display: inline-block;
    }
    p {
      font-size: 20px;
      font-weight: 700;
      padding-top: 5px;
      padding-bottom: 30px;
    }
  }
  .form-group {
    display: flex;
    align-items: center;
    .group {
      position: relative;
      margin-right: 40px;
      input {
        padding: 10px;
        display: block;
        width: 240px;
        height: 100%;
        border: none;
        border-bottom: 1px solid #cccccc;
        background: transparent;
        &:focus {
          outline: none;
        }
        &:focus ~ label,
        &:valid ~ label {
          top: -15px;
          font-size: 14px;
          font-style: normal;
          color: #fb0275;
        }
        &:focus ~ .bar:before,
        &:focus ~ .bar:after {
          width: 50%;
        }
      }
      label {
        font-size: 14px;
        font-style: italic;
        position: absolute;
        pointer-events: none;
        left: 10px;
        top: 15px;
        transition: 0.2s ease all;
      }
      .bar {
        position: relative;
        display: block;
        width: 250px;
        &:before,
        &:after {
          content: "";
          height: 2px;
          width: 0;
          bottom: 0;
          position: absolute;
          background: #fb0275;
          transition: 0.2s ease all;
        }
        &:before {
          left: 50%;
        }
        &:after {
          right: 50%;
        }
      }
    }
    button {
      @include button;
      padding: 15px 45px;
      position: relative;
    }
  }
}

@mixin owl-nav {
  margin-top: 25px;
  position: relative;
  .owl-prev,
  .owl-next {
    width: 90px;
    height: 20px;
    color: #883eca;
    position: absolute;
    outline: none;
    &:hover {
      background-color: #f9f6f9;
    }
    span {
      display: inline-block;
    }
    .arrow {
      font-weight: 800;
      vertical-align: middle;
    }
    .line {
      background-color: #883eca;
      width: 80px;
      height: 2px;
      vertical-align: middle;
    }
  }
  .owl-prev {
    top: 0px;
    left: 220px;
  }
  .owl-next {
    top: 0px;
    left: 380px;
  }
}
