@media only screen and (max-width: 1200px) {
  .nav {
    display: none;
  }
  header {
    .connect {
      button {
        margin-left: 8%;
      }
    }
    .menu {
      .menu-list {
        padding-left: 0;
      }
    }
    .banner-block {
      .text-block {
        h1 {
          font-size: 50px;
          padding-right: 29%;
          padding-bottom: 50px;
        }
        .order-block {
          align-items: center;
          button {
            padding: 16px 20px;
            margin-bottom: 0;
          }
        }
      }
    }
  }
  
  .about-block {
      .wrapper {
          .text-block {
          padding-bottom: 80px;
          width: 45%;
          h3 {
              padding-right: 10%;
          }
          }
      }
  }
  
  main {
    .services1 {
      .text-block {
        padding-right: 5%;
        padding-bottom: 60px;
        h3 {
          font-size: 28px;
          padding-bottom: 40px;
        }
        ul {
          padding-bottom: 40px;
        }
      }
    }
    .services2 {
      .text-block {
        padding-bottom: 500px;
      }
      .image {
        img {
          left: -450px;
        }
      }
    }
    .services3 {
      background-position: 20px 265px, 530px -10px;
      .text-block {
        form {
          padding-bottom: 100px;
        }
      }
    }
  }
  .feedback {
    .feedback-slider {
      .owl-carousel {
        width: 100%;
        left: 0;
        &::before,
        &::after {
          width: 0;
          height: 0;
        }
        .item {
          width: 98%;
        }
        .owl-nav {
          .owl-prev {
            left: 80px;
          }
          .owl-next {
            left: 240px;
          }
        }
      }
    }
  }
  .faq {
    .faq-items {
      .item {
        max-width: 50%;
        padding: 40px 25px 40px 0;
      }
    }
    form {
      .text {
        padding-left: 0;
      }
      .form-group {
        padding-left: 0;
        .group {
          margin-right: 1%;
        }
      }
    }
  }
  footer {
    .contents {
      p:last-child {
        display: none;
      }
    }
  }
}

@media only screen and (max-width: 992px) {
  body.lock {
    overflow: hidden;
  }
  header {
    min-height: 600px;
    padding-top: 20px;
    background-image: linear-gradient(-45deg, #6a3093, #a044ff);
    background-position: 0 0;
    .hamburger-menu {
      display: block;
    }
    .logo {
      margin-bottom: 40px;
    }
    .menu {
      .menu-list {
        display: none;
      }
    }
    .connect {
      display: none;
    }
    .banner-block {
      width: 100%;
      .banner {
        img {
          width: 120%;
          top: -50px;
        }
      }
      .text-block {
        padding-left: 0;
        h1 {
          font-size: 45px;
          padding-right: 15%;
          padding-bottom: 30px;
          span {
            display: block;
          }
        }
        h2 {
          font-size: 21px;
          padding-right: 0;
          padding-bottom: 30px;
        }
      }
      .order-block {
        flex-wrap: wrap;
        margin-bottom: 20px;
        button {
          margin-bottom: 30px;
        }
        p {
          padding-left: 0px !important;
        }
      }
    }
  }
  .about-block {
      height: auto;
    .wrapper {
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
    .icons-block {
        width: 740px;
        height: 500px;
        margin-left: 2%;
        margin-top: 0;
        background: url(../imgs/arrows-md.png) no-repeat 100px 0px;
        .item1 {
            left: 180px;
            top: -50px;
        }
        .item2 {
            left: 10px;
            top: 125px;
        }
        .item3 {
            top: 40px;
            left: 570px;
        }
        .item4 {
            top: 255px;
            left: 375px;
        }
      }
      .text-block {
        width: 98%;
        margin-left: 1%;
        margin-right: 1%;
      }
    } 
  }
  main {
    .services1 {
      .small-lines {
        left: 50%;
      }
      .text-block {
        h2 {
          padding-top: 70px;
          padding-bottom: 70px;
        }
        form {
          max-width: 100%;
          width: 100%;
          position: absolute;
          display: flex;
          justify-content: space-between;
          div {
            display: flex;
            justify-content: space-between;
            width: 60%;
            span,
            p {
              display: inline-block;
              padding-right: 10px;
            }
          }
        }
      }
      .poster-slider {
        margin-top: 70px;
        margin-bottom: 270px;
      }
    }
    .services2 {
      background: url(../imgs/sofa.png) no-repeat 50px 100px;
      .image {
        display: none;
      }
    }
    .services3 {
      background-position: -150px 550px, 330px 490px;
      .small-lines {
        left: 50%;
      }
      .text-block {
        form {
          max-width: 30%;
          padding-top: 55px;
          .form-group {
            display: block;
            .group {
              margin-right: 0;
              margin-bottom: 30px;
            }
            button {
              padding: 15px 70px;
            }
          }
        }
      }
    }
  }
  .feedback {
    h2 {
      padding-top: 70px;
    }
    .feedback-slider {
      .owl-carousel {
        .item {
          width: 100%;
          margin-left: 0;
          margin-right: 0;
        }
        .owl-nav {
          .owl-prev {
            left: 0;
          }
          .owl-next {
            left: 180px;
          }
        }
      }
    }
  }
  .faq {
    h2 {
      padding-top: 40px;
    }
    .faq-items {
      .item {
        max-width: 100%;
        padding: 20px 0 20px 0;
      }
    }
    form {
      margin-top: 20px;
      margin-bottom: 100px;
      position: relative;
      .text {
        padding-left: 0;
        margin-left: 0;
      }
      .form-group {
        margin-left: 0;
        padding-left: 0;
      }
      .question-mark {
        top: -60px;
        left: 500px;
      }
    }
  }
  .contacts-block {
    .contacts {
      padding: 50px 60px;
      top: -420px;
      h2 {
        padding-top: 0px;
        padding-bottom: 30px;
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  header {
    height: 100%;
    .logo {
      margin-bottom: 0px;
    }
    .banner-block {
      flex-direction: column;
      align-items: center;
      justify-content: center;
      .banner {
        width: 100%;
        height: 450px;
        img {
          width: 80%;
          height: auto;
          left: 0;
        }
      }
      .text-block {
        width: 98%;
        min-height: 300px;
        margin-left: 1%;
        margin-right: 1%;
        h1 {
          font-size: 36px;
          span {
            padding-right: 0px;
          }
        }
      }
      .order-block {
        button {
          margin-right: 5%;
          margin-bottom: 0;
        }
      }
    }
  }
  .about-block {
      .wrapper {
        .text-block {
            padding-top: 40px;
            h2 {
              padding-bottom: 40px;
            }
        }
        .icons-block {
            background: url(../imgs/arrows-sm.png) no-repeat 50px 30px;
            height: 800px;
            width: 290px;
            .item1 {
              left: 20px;
              top: -60px;
            }
            .item2 {
              left: -60px;
              top: 240px;
            }
            .item3 {
              top: 145px;
              left: 110px;
            }
            .item4 {
              top: 490px;
              left: 50px;
            }
        }
    }
  }
  main {
    .services2 {
      background: url(../imgs/sofa.png) no-repeat 0px 200px;
    }
    .services3 {
      background-color: #faf8fb;
      background-image: url(../imgs/style.png);
      background-repeat: no-repeat;
      background-position: 0px 420px;
      .text-block {
        form {
          max-width: 100%;
          padding-top: 500px;
        }
      }
    }
  }
  .feedback {
    min-height: 700px;
    .feedback-slider {
      .owl-carousel {
        .item {
          flex-direction: column;
          img {
            margin-left: 0;
            margin-top: 40px;
          }
          .text-block {
            padding-left: 5%;
            padding-right: 5%;
          }
        }
      }
    }
  }
  .faq {
    form {
      background-image: none;
      .question-mark {
        top: 0;
        left: 350px;
        width: 150px;
        height: 150px;
      }
      .form-group {
        flex-direction: column;
        align-items: flex-start;
        button {
          margin-top: 20px;
          width: 50%;
        }
      }
    }
  }
}

@media only screen and (max-width: 576px) {
  header {
    .hamburger-menu {
      .hamburger-list {
        width: 100%;
      }
    }
    .banner-block {
      .banner {
        height: 300px;
        img {
          max-width: 400px;
          margin-left: 30px;
        }
      }
      .order-block {
        button {
          margin-right: 5%;
          margin-bottom: 30px;
        }
      }
    }
  }
  
  main {
    .services1 {
      .poster-slider {
        .text {
          span {
            top: -30px;
            left: 0;
          }
        }
      }
      .text-block {
        form {
          flex-direction: column;
          bottom: -550px;
          div {
            width: 100%;
            flex-wrap: wrap;
            align-items: flex-start;
            padding-top: 10px;
          }
          button {
            width: 100%;
            margin-bottom: 10px;
          }
        }
      }
    }
    .services3 {
      background-position: 0 480px;
      .text-block {
        form {
          .form-group {
            .group {
              input {
                width: 100%;
              }
            }
            button {
              width: 100%;
            }
          }
        }
      }
    }
  }
  .faq {
    padding-bottom: 30px;
    form {
      .form-group {
        align-items: center;
        .group {
          margin-right: 0;
          input {
            width: 100%;
          }
        }
        button {
          width: 100%;
          margin-bottom: 10px;
        }
      }
      .question-mark {
        left: 200px;
      }
    }
  }
}
