@import "normalize";
@import "fonts";
@import "mixins";

$orange: #f2ac04;
$gray: #565656;

* {
  margin: 0px;
  padding: 0px;
}

@media (max-width: 1200px) {
  .container {
    max-width: 1170px;
  }
}

.row {
  text-align: center;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  outline: none;
}

body {
  font-family: "Open Sans", sans-serif;
  background-image: url(../imgs/lines.png);
  background-repeat: repeat-y;
  background-position: center;
  background-size: initial;
  a:hover,
  a:active {
    color: #f2f1f3;
  }
}

.preloader {
  --duration: 1s;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #3d3a56;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: var(--duration);
  .preloader-content {
    position: relative;
    width: 200px;
    height: 200px;
    .preloader-first,
    .preloader-second,
    .preloader-third {
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      animation: 1s linear infinite;
    }
    .preloader-first {
      border-bottom: 3px solid #ff7473;
      animation-name: first;
    }
    .preloader-second {
      border-right: 3px solid #ffc952;
      animation-name: second;
    }
    .preloader-third {
      border-top: 3px solid #47b8e0;
      animation-name: third;
    }
    @keyframes first {
      0% {
        transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
      }
      100% {
        transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
      }
    }
    @keyframes second {
      0% {
        transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
      }
      100% {
        transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
      }
    }
    @keyframes third {
      0% {
        transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
      }
      100% {
        transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
      }
    }
  }
}

.nav {
  position: fixed;
  top: calc(50% - 150px);
  right: 2%;
  z-index: 500;
  .nav-list {
    list-style: none;
    width: 25px;
    li:not(:nth-child(3)) {
      padding-bottom: 30px;
    }
    li {
      a.mPS2id-highlight {
        content: url(../imgs/buttons/active-circle.png);
        margin-left: -3px;
      }
    }
    .small-nav-list {
      list-style: none;
      li {
        padding-bottom: 15px;
        &:first-child {
          padding-top: 15px;
        }
        a.mPS2id-highlight {
          content: url(../imgs/buttons/active-line.png);
        }
      }
    }
  }
}

.popup-overlay {
  display: none;
  position: fixed;
  z-index: 900;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.popup {
  display: none;
  position: fixed;
  left: 50%;
  top: 20%;
  margin-left: -180px;
  z-index: 901;
  max-height: 90%;
  overflow: auto;
  .popup-content {
    position: relative;
    .popup-close {
      position: absolute;
      top: 10px;
      right: 10px;
      text-align: center;
      border: none;
      color: white;
      background-color: transparent;
      font-size: 30px;
      outline: none;
    }
    background-image: linear-gradient(-45deg, #6a3093, #a044ff);
    @include big-form;
    width: 360px;
    min-height: 370px;
    color: white;
    .text {
      padding-top: 50px;
      padding-left: 50px;
    }
    .form-group {
      flex-direction: column;
      padding-bottom: 50px;
      .group {
        margin-right: 0;
        padding-bottom: 30px;
      }
    }
  }
}

header {
  color: #ffffff;
  background-image: url(../imgs/figure1.png), url(../imgs/figure2.png),
    linear-gradient(-45deg, #6a3093, #a044ff);
  background-size: cover;
  background-position: -220px 480px, -70px 510px, 0px 0px;
  background-repeat: no-repeat, no-repeat, no-repeat;
  width: 100%;
  min-height: 850px;
  max-height: 1000px;
  height: 100vh;
  padding-top: 68px;
  position: relative;
  z-index: 10;
  .logo {
    margin-bottom: 85px;
    display: flex;
    align-items: center;
    height: 36px;
    a {
      font-size: 18px;
      font-weight: 800;
      text-transform: uppercase;
      color: white;
      &:hover,
      &:active {
        color: #f2f1f3;
      }
    }
  }
  .menu {
    .menu-list {
      display: flex;
      justify-content: space-around;
      align-items: center;
      padding-left: 30px;
      height: 36px;
      li {
        list-style-type: none;
        a {
          font-size: 13px;
          font-weight: 600;
          color: white;
          text-transform: uppercase;
          &:hover,
          &:active {
            color: #f2f1f3;
          }
        }
      }
    }
  }
  // hamburger-menu
  .hamburger-menu {
    display: none;
    .hamburger-button {
      width: 35px;
      height: 35px;
      background-color: transparent;
      border: none;
      position: fixed;
      right: 15px;
      top: 40px;
      z-index: 999;
      overflow: hidden;
      outline: none;
      transition: 0.4s;
      span {
        display: block;
        position: absolute;
        background-color: #fb0275;
        height: 5px;
        width: 35px;
        top: 15px;
        &::after,
        &::before {
          display: block;
          position: absolute;
          background-color: #fb0275;
          height: 5px;
          width: 35px;
          content: "";
        }
        &::after {
          bottom: 10px;
        }
        &::before {
          top: 10px;
        }
      }
    }
    .hamburger-button-line {
      span {
        &::after {
          transition: transform 0.2s ease-in, bottom 0.2s linear 0.2s;
        }
        &::before {
          transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
        }
      }
    }

    .hamburger-list {
      width: 30%;
      height: 100%;
      position: fixed;
      background-color: #0d0d0d;
      opacity: 0.9;
      z-index: 998;
      padding-top: 40px;
      top: 0;
      right: 0;
      transition: 0.5s;
      overflow: auto;
      display: none;
      li {
        list-style-type: none;
        padding: 10px;
        border-bottom: 1px solid #afafaf;
        margin-left: 10%;
        margin-right: 50px;
        a {
          font-size: 18px;
          color: #ffffff;
          font-family: "Open Sans";
        }
        &:hover {
          text-decoration: underline;
          filter: brightness(0.7);
        }
      }
    }
    .hamburger-overlay {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      z-index: 997;
    }
    &_active {
      .hamburger-button-line {
        span {
          width: 0;
          &::after,
          &::before {
            transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
          }
          &::after {
            bottom: 0;
            transform: rotate(45deg);
          }
          &::before {
            top: 0;
            transform: rotate(-45deg);
          }
        }
      }
      .hamburger-overlay {
        display: block;
      }
      .hamburger-list {
        display: block;
      }
    }
  }

  .connect {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .tel {
      font-size: 16px;
      font-weight: 700;
      padding-left: 15px;
      padding-right: 10px;
    }
    button {
      display: flex;
      align-items: center;
      background: transparent;
      padding: 10px 22px;
      border: 2px solid white;
      border-radius: 25px;
      span {
        font-size: 11px;
        font-weight: 800;
        color: white;
        text-transform: uppercase;
      }
    }
  }
  .banner-block {
    display: flex;
    .banner {
      position: relative;
      width: 50%;
      img {
        position: absolute;
        top: 0px;
        left: -85px;
      }
    }
    .text-block {
      width: 50%;
      text-align: left;
      padding-left: 55px;
      h1 {
        font-family: "Muller";
        font-size: 60px;
        font-weight: 800;
        text-transform: uppercase;
        padding-right: 100px;
        padding-bottom: 90px;
        margin: 0;
        span {
          display: block;
          padding-right: 70px;
        }
      }
      h2 {
        font-family: "Muller";
        font-size: 32px;
        font-weight: 500;
        padding-right: 70px;
        padding-bottom: 80px;
      }
      .order-block {
        display: flex;
        align-items: center;
        padding-right: 20px;
        margin-bottom: 80px;
        button {
          @include button;
          padding: 16px 48px;
        }
        p {
          font-size: 16px;
          font-family: "Open Sans";
          font-weight: 400;
          padding-left: 30px;
          max-width: 205px;
        }
      }
    }
  }
}

